/**
 * @module ngeo.datasource.DataSources
*/
const exports = {};
import ngeoBase from 'ngeo/index.js';
import olCollection from 'ol/Collection.js';


/**
 * @type {!angular.Module}
 */
exports.module = angular.module('ngeoDataSources', []);
// DataSources with the ngeox.datasource.DataSources type.
exports.module.value('ngeoDataSources', new olCollection());
ngeoBase.module.requires.push(exports.module.name);


export default exports;



